@import url(https://fonts.googleapis.com/css2?family=Amiri&family=Mulish:ital,wght@1,300&display=swap);
.announcement-container{background-color:#2c2e2f;display:flex;align-items:center;justify-content:space-between;padding:0.1rem 1.5rem}.announcement-container a{color:white;text-decoration:none;line-height:30px}


.menu-container{display:flex}.menu-container .menu-item{color:white;font-weight:600;font-size:1.2rem}.menu-container .menu-item:hover{color:black;text-decoration:underline}.menu-container.align-left{flex-direction:row}.menu-container.align-left :first-child{margin-right:1.7rem}.menu-container.align-right{flex-direction:row-reverse}.menu-container.align-right :first-child{margin-left:1.7rem}

.logo-container .logo:hover{opacity:0.7}

.navbar-container{display:grid;grid-template-columns:2fr auto 2fr;grid-column-gap:2rem;-webkit-column-gap:2rem;column-gap:2rem;padding:20px 25px 10px 25px;align-items:center;z-index:99;position:fixed;top:calc(0.2rem + 30px);width:100%;transition:all 0.1s ease-in}.navbar-container.scrolled{background-color:white;border-bottom:solid 1px #efefee;box-shadow:0 4px 8px -8px black;padding:15px 25px 10px 25px;top:0}.navbar-container.scrolled a{color:#45464c}.navbar-container:hover{background-color:white;border-bottom:solid 1px #efefee;box-shadow:0 4px 8px -8px black}.navbar-container:hover a{color:#45464c}

.footer-container{background-color:crimson}

.layout-container{display:flex;flex-direction:column;position:relative}

.section-container{min-height:100vh;background-color:burlywood}

.block-quote{font-family:"Amiri", serif;font-size:2rem;line-height:3rem;margin-top:30vh;text-align:center;background-color:white;opacity:0.7;width:40rem;border:1px solid black;z-index:9}

.blockquote-container{display:flex;justify-content:center;align-items:center}.button-container{display:flex;justify-content:center;align-items:center;margin-top:1.5rem}

